/*!
 * This is for all the admin styles.
 */

.instagram{
	margin: 20px 0px;
}
.instagram label {
	color: #555;
    display: inline-block;
    margin-top: 10px;
    margin-bottom: 9px;
    padding: 15px 10px;
    line-height: 1.3;
    width: 20%;
    font-size: 14px;
}
.instagram input[type="radio"]{
  width:auto;
  margin-right: 5px;
  margin-top: 0px;
  margin-left: 5px;
}
.instagram input,.instagram input[type="text"],.instagram select,.instagram textarea,.instagram input[type="number"],.instagram input[type="url"]{
	display: inline-block;
	width: 15%;
}
.instagram input[type="submit"]{
	width: auto;
}
.submitter{
	display: inline-block;
	margin-bottom: 30px;
	margin-left: 20px;
}
#dashboard_right_now.instagram li span{
	color: #fff;
}
#dashboard_right_now.instagram li{
	background-color: transparent;
}
#dashboard_right_now.instagram li[title]{
	padding-right: 10px;
	color: #fff;
	background-color: #44d176;
	font-weight: 700;
	border: none;
	line-height: 10px;
}
#dashboard_right_now.instagram li>span{
	padding: 10px 6px 10px 4px;
	margin-right: 0px;
}
.instagram-settings{
  min-width: 255px;
  border: 1px solid #e5e5e5;
  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.04);
  box-shadow: 0 1px 1px rgba(0,0,0,.04);
  background: #fff;
  margin-bottom: 20px;
  padding: 0;
  line-height: 1;
  width: 99%;
  margin-top: 40px;
}
.instagram-settings h2{
	padding: 0px 20px;
}

.wp-list-table {

	th#preview,
	th#status {
		width: 10%;
	}
}

#dashboard_right_now.instagram li>span:before{
	content: none;
}
#dashboard_right_now li{
	width: auto;
}

#dashboard_right_now .radio-check ul.wc-radios{
  display: inline-block;
  width: auto;
  line-height: normal;
  margin-top: 15px;
  vertical-align: middle;
}
.instagramoutletproductlist input[type="number"]{
  max-width: 100%;
}

.instagram-wrap h3.disabled{
	display: inline-block;
	font-size: 14px;
	font-weight: 400;
	color: #32373c;
	width: 14%;
	padding: 6px;
	border: 1px solid #ddd;
	box-shadow: inset 0 1px 2px rgba(0,0,0,.07);
}

.instagram-disable {
	color: #4ac10d;
	font-weight: 700;
	background-color: #a0f9b359;
}
.instagram-green{
	background-color: #46da16;

}
.insta-top{
	margin-top: 20px !important;
}
.thickbox-header{
	margin: 10px 0px;
	display: inline-block;
	width: 100%;
}
.thickbox-header button{
	float: right;
}
.thickbox-body img{
	width: 100%;
	height: 100%;
	cursor: pointer;
	object-fit: cover;
	object-position: top;
}
.thickbox-body .img-wrap{
	display: inline-block;
	vertical-align: top;
	box-sizing: border-box;
	border: 1px solid #ccc;
	margin: 5px;
	position: relative;
	width: 135px;
	height: 135px;
	font-size: 0;
	border-radius: 3px;
}
.thickbox-body .img-wrap.selected img{
	opacity: .1;
}
.thickbox-body .img-wrap.selected::before{
	content: "L";
	position: absolute;
	font-family: arial;
	-ms-transform: scaleX(-1) rotate(-35deg); /* IE 9 */
	-webkit-transform: scaleX(-1) rotate(-35deg); /* Chrome, Safari, Opera */
	transform: scaleX(-1) rotate(-35deg);
	top: 0px;
	left: 5px;
	font-size: 24px;
	font-weight: bold;
	color: #287dda;
}
.woocommerceinstagramtagsdata .preview{
	float: none;
}

.tt .datatable{
	text-align: left;
	width: 100%;
	background-color: #fff;
	padding: 20px;
}

#custom_options .addButton{
	margin:20px 20px 0px;
}
.tt .datatable th{
	font-weight: 400;
	color: #555;
	vertical-align: bottom;
	padding: 10px;
	border-bottom: 1px solid rgba(0, 0, 0, 0.12);
}
.tt .datatable td{
	padding: 5px 10px;

}
.tt .datatable label{

	float: none;
	width: auto;
	margin:0px;

}
div#field {
	width: 95%;
	background-color: #eee;
	padding: 10px;
	margin: 10px;
}
.tt .datatable .datath1{
	width: 20%;
}

.tt .datatable .datath2{
	width: 20%;
}

.tt .datatable .datath3{
	width: 25%;
}

.tt .datatable .datath4{
	width: 25%;
}
.pre-loader{
	position: relative;
	height: 450px;
	background: #F5F5F5;
}
.pre-loader::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
		right: 0;
		bottom: 0;
    margin: auto;
    border: 3px solid #f3f3f3;
    border-radius: 50%;
    border-top: 3px solid #379bf0;
    width: 50px;
    height: 50px;
    -webkit-animation: spin 0.6s linear infinite;
    animation: spin 0.6s linear infinite;
}
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}